import '../design-systems/system-1/styles/variables.css';

/** @type { import('@storybook/react').Preview } */
const preview = {
  parameters: {
    controls: {
      matchers: {
       color: /(background|color)$/i,
       date: /Date$/i,
      },
    },
    // 스토리북 사이드바 순서 설정
    sidebar: {
      order: ['Concepts', 'Tokens', 'component', '*'],
    },
    options: {
      storySort: {
        order: ['Concepts', 'Tokens', 'component', '*'],
        method: 'alphabetical',
      }
    },
    // 문서 레이아웃 여백 설정
    docs: {
      canvas: {
        // 문서 캔버스 주변의 패딩 제거
        withToolbar: true, 
        margin: 0,
        padding: 0
      },
      // 문서 컨테이너 스타일 설정
      container: {
        padding: '0'
      },
      // 문서 페이지 스타일 설정
      page: {
        padding: '0'
      }
    }
  },
};

export default preview;